<script>
// @ is an alias to /src
import gurus from '../assets/js/guru-data';

export default {
	name: 'home',
	data() {
		return {
			showSidepanel: false,
			menuShown: false,
			widthLT960: false,
			gurus: gurus
		}
	},
	created() {
		var vm = this;
		if (window.innerWidth < 860) {
			vm.widthLT960 = true;
		}
	},
	methods: {
		logOut() {
			localStorage.clear();
			location.reload();
		},
		goTo(name) {
			var vm = this;
			if (vm.$route.path != '/' + name) {
				vm.$router.push('/' + name);

			}
			if (vm.widthLT960) {
				vm.menuShown = false;
			}
		}
	}
}
